.cell {
	width: 35px;
	height: 35px;
	outline: 1px solid black;
	display: inline-block; 
}

.cell-visited {
	background-color:blue;
}

.cell-start {
	background-color: green;
}

.cell-end {
	background-color: red;
}

.cell-wall {
	background-color: black;
}

.cell-path {
	background-color:yellow;
}
